import React from 'react'
import {NavLink} from 'react-router-dom'
import {Button,Navbar,Nav} from 'react-bootstrap'
import './Rjnavbar.css'
import ixar from '../components/pages/img/ixar.png'

const Rjnavbar = () => {
  return (
    <>
      <Navbar
        className="navbar"
        collapseOnSelect
        expand="lg"
        bg="dark"
        variant="dark"
        
      >
        <Navbar.Brand href="#home">
          {" "}
          <img src={ixar} style={{height:"25px"}} alt="home" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
        <ul className="nav navbar-nav navbar-center "> 
          <Nav className="mr-auto " >
          
            <Nav.Link href="ixarwebsite">HOME</Nav.Link>
            <Nav.Link href="services">SERVICES</Nav.Link>
            <Nav.Link href="product">PRODUCTS</Nav.Link>
            <Nav.Link href="team">TEAM</Nav.Link>
            <Nav.Link href="contact">CONTACT</Nav.Link>
            {/* <Nav.Link href="#call">CALL</Nav.Link> */}
            <ul className="nav  navbar-nav navbar-right" style={{padding:"0px 0px 0px 20%"}}>
            <a
              href="tel: 9930652916"
              style={{ textDecoration: "none", color: "white" }}
            >
              <button type="button" class="btn btn-success">
                Call
                </button>
            </a>
          </ul>
          </Nav>
          </ul>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
}
export default Rjnavbar;